*{
    margin:0;
    padding:0;
  }

.TechSkills{
    width:90%;
    height: 320px;
    
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
   border:0px solid red;
}

.TechSkills img{
    width:100%;
    height: 180px;
}

.backend{
    width:90%;
    height:320px;
    padding:20px;
    box-shadow:rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border:0px solid yellow;
}

.backend img{
    width:100%;
    height:180px;
}

@media (max-width:320px){
    .backend img , .TechSkills img{
        width:100%;
        height:80px;
    }
    .TechSkills,.backend{
        width:96%;
        height:160px;
        padding:7px;
    }
}

@media(min-width:321px){
    .backend img, .TechSkills img{
        width:100%;
        height:120px;
    }
    .TechSkills,.backend{
        width:90%;
        height:240px;
        padding:10px;
    }
}